.championGenerator{
    .flex {
        display: flex;
        padding-top:1rem;
        flex-wrap: wrap;
        justify-content: center;
        button {
            border-style: none;
            cursor: pointer;
            box-sizing: border-box;
            background-color: black;
            color: white;
            width: 8rem;
            height: 4rem;
            padding:.7rem;
            border-radius: 2rem;
            &:hover {
                border:white solid 2px;
            }
            margin:0.6rem 0.6rem 0.6rem 0;
        }
    }
}