.championGenerator {
    background-color: grey;
    height: 100%;
    width: 100%;
        .grid {
            padding-top:2rem;
            width:100%;
            display:grid;
            grid-template-columns: repeat(auto-fit, 8rem);
            grid-column-gap: 20px;
            grid-row-gap: 20px;
            justify-content: center;
            img {
                width: 100%;
            }
        }
}